class Cache{
    constructor(name){
        this.cache = localStorage.getItem(name) ? JSON.parse(localStorage.getItem(name)) : {};
    }

    set(key, value){
        this.cache[key] = value;
        localStorage.setItem(name, JSON.stringify(this.cache));
    }

    get(key){
        return this.cache[key];
    }

    delete(key){
        delete this.cache[key];
        localStorage.setItem(name, JSON.stringify(this.cache));
    }
}

const simpleCache = new Cache("cachedForYourConvenienceOnly");

export default simpleCache;